.logo {
  position: absolute;
  width: 60px;
  height: 30px;
  left: 23px;
  top: 23px;

  @media screen and (min-width: 768px) {
    width: 100px;
    height: 50px;
    left: 40px;
    top: 40px;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }

  @media screen and (min-width: 1280px) {
    width: 135px;
    height: 70px;
    left: 40px;
    top: 40px;
  }
}

.logo-img {
  width: 60px;
  height: 30px;

  @media screen and (min-width: 768px) {
    width: 100px;
    height: 50px;
  }

  @media screen and (min-width: 1280px) {
    width: 135px;
    height: 70px;
  }
}
